      <h3> <span class="underline-r">aboutMe</span> </h3>
        <div class="column column-5 introduction-info-wrapper">
          <p>
          My name is Danijel and I am a twenty-four year old student at Zagreb University of Applied Sciences. I currently live in both Đurmanec ( near Krapina ) and Zagreb. I am a tireless seeker of knowledge, occassional lover of solving problems and also programmer/full stack web developer. I strongly believe technology can, and should enrich every single life this universe has to offer. I am currently working as a software engineer at <a href="https://www.infobip.com/" target="_blank" class="slide-i-o cl-prime"> Infobip ltd. </a> here in Croatia. When I'm not coding or pushing pixels around, you'll find me in the gym or football field. I also like to eat and cook healthy, watch movies, read scientific articles, reddit, smartphones, cars and Batman! Did I mention 9gag <span class="cl-prime"> ? </span>
          </p>
        </div>

        <div class="column column-7 introduction-id-wrapper">
          <div class="img-circular">
          </div>

          <br>
          
          <h3 class="text-center b-b">
            Software engineer <span class="cl-prime">&&</span> Web developer
          </h3>
