          Interested already? See 
          <router-link to="/contact" class="slide-i-o cl-prime">
                            Contact
          </router-link>
          section. You can also check out 
          <router-link to="/project" class="slide-i-o cl-prime">
                            Projects
            </router-link>
          or 
          <router-link to="/about/timeline" class="slide-i-o cl-prime">
